*{
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
.short, .video{
    display: flex;
    flex-wrap: wrap;
}
.abc{
    margin: 5px;
    display: flex;
}
.video_text{
    margin: -10px 10px;
}
.shortvd{
    width: 230px;
    margin: 6px;
}
.videovd{
    width: 350px;
    margin: 6px;
}

.imgvd{
    width: 100%;
    height: 200px;
    background-color: rgb(63, 72, 72);
}
.imgdiv{
    margin: 15px;
    border-radius: 10px;
    width: 95%;
    height: 400px;
    background-color: rgb(150, 197, 197);
}
.avt{
    width: 50px;
    height: 50px;
    border-radius: 110%;
    background-color: brown;

}
.header{
    margin-top: 0px;
    border-bottom: solid black 2px;
    position: fixed;
    top: 0px;
    background-color: aliceblue;
}
a{
    text-decoration: none;
    color: black;
}
.main{
    margin-top: 120px ;
}
img{
    width: 100% ;
    height: auto;
}
input{
    width: 100% ;
    height: 100%;
}
.menu, .category{
    display: flex;
}
.logo{
    width: 8%;
}
.search{
    width: 50%;
    padding: 1% 0%;
    margin: 0px 15%;
}
.user_icon{
    padding-top: 1%;
    font-size: 30px;
}
.category>.cate{
    font-size: 20px;
    margin: 5px;
    background-color: rgb(219, 219, 219) ;
    padding: 8px;
    border-radius: 10px;
}
#search_btn{
    border-radius: 15px;
    padding: 2px 10px;
    font-size: 20px;
}